import { HostListener, OnDestroy, Directive } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {
  BillpayDiscardChangesFormComponent,
  BillpayDiscardChangesModalService,
} from '@backbase/billpay-journeys-common';
import { Observable, of, Subject } from 'rxjs';
import { map, shareReplay, switchMap, take, takeUntil, tap } from 'rxjs/operators';

import { BillpayManagePayeeStateService } from '../services/billpay-manage-payee-state.service';
import { ElectronicPayeeForm } from './payee-form.model';

@Directive()
export abstract class BaseElectronicPayeePageDirective implements OnDestroy, BillpayDiscardChangesFormComponent {
  protected isFormTouched = false;
  protected readonly payeeState$: Observable<ElectronicPayeeForm | undefined>;
  protected readonly id$: Observable<string>;
  protected readonly destroy$ = new Subject<void>();
  isFindPayeeModal = false;

  /** @internal */
  protected constructor(
    protected readonly route: ActivatedRoute,
    protected readonly stateService: BillpayManagePayeeStateService,
    protected readonly discardModalService: BillpayDiscardChangesModalService,
  ) {
    this.id$ = this.route.paramMap.pipe(
      take(1),
      map(params => params.get('id') || ''),
    );

    this.payeeState$ = this.stateService.electronicPayeeFormState.pipe(take(1), shareReplay(1));
  }

  @HostListener('window:beforeunload', ['$event'])
  onWindowUnload($event: BeforeUnloadEvent) {
    this.payeeState$.subscribe(state => {
      if ((state || this.isFormTouched) && !this.isDiscardModalOpen) {
        $event.returnValue = true;
      }
    });
  }

  get isDiscardModalOpen() {
    return this.discardModalService.isDiscardModalOpen;
  }

  onFormValueChanged() {
    this.isFormTouched = true;
  }

  openModal(): Observable<boolean> {
    const openModal = () =>
      this.discardModalService
        .openModal()
        .pipe(tap(isResolved => isResolved && this.stateService.resetElectronicPayee()));

    return this.payeeState$.pipe(
      switchMap(payeeState => (payeeState || this.isFormTouched ? openModal() : of(true))),
      takeUntil(this.destroy$),
    );
  }

  onModalResolve(resolvedWith: boolean) {
    this.isFindPayeeModal = false;
    this.discardModalService.onModalResolve(resolvedWith);
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
