import { Injectable, Optional } from '@angular/core';
import { ActivatedRoute, NavigationExtras, Router } from '@angular/router';
import { CommunicationService } from '../communication.service';
import { ManagePayeesJourneyConfigService } from './manage-payees-journey-config.service';

@Injectable()
export class ManagePayeesNavigationService {
  private readonly defaultRelative: NavigationExtras;

  constructor(
    private readonly router: Router,
    private readonly route: ActivatedRoute,
    private config: ManagePayeesJourneyConfigService,
    @Optional() private communicationService: CommunicationService,
  ) {
    this.defaultRelative = { relativeTo: this.route };
  }

  navigateToPayeeList(extras?: NavigationExtras) {
    if (this.config.multipleBillsMode) {
      this.communicationService?.navigateToMultiplePaymentsForm();
    } else {
      this.router.navigate(['./'], extras || this.defaultRelative);
    }
  }

  navigateToSearch(extras?: NavigationExtras) {
    this.router.navigate(['payees'], extras || this.defaultRelative);
  }

  navigateToManualPayeeForm(extras?: NavigationExtras) {
    this.router.navigate(['payees/manual'], extras || this.defaultRelative);
  }

  navigateToManualPayeeReview(extras?: NavigationExtras) {
    this.router.navigate(['payees/manual/review'], extras || this.defaultRelative);
  }

  navigateToEditManualPayeeForm(id: string, extras?: NavigationExtras) {
    this.router.navigate(['payees/manual', 'edit', id], extras || this.defaultRelative);
  }

  navigateToEditManualReview(id: string, extras?: NavigationExtras) {
    this.router.navigate(['payees/manual/edit', id, 'review'], extras || this.defaultRelative);
  }

  navigateToElectronicPayeeForm(id: string, extras?: NavigationExtras) {
    this.router.navigate(['payees/electronic', id], { ...this.defaultRelative, ...extras });
  }

  navigateToElectronicPayeeReview(id: string, extras?: NavigationExtras) {
    this.router.navigate(['payees/electronic', id, 'review'], { ...this.defaultRelative, ...extras });
  }

  navigateToEditElectronicPayeeForm(id: string, extras?: NavigationExtras) {
    this.router.navigate(['payees/electronic', 'edit', id], extras || this.defaultRelative);
  }

  navigateToEditElectronicPayeeReview(id: string, extras?: NavigationExtras) {
    this.router.navigate(['payees/electronic', 'edit', id, 'review'], extras || this.defaultRelative);
  }

  navigateToPayeeSummary(payeeID: string, extras?: NavigationExtras) {
    this.router.navigate(['payee-summary', { payeeID }], extras || this.defaultRelative);
  }

  navigateToPayveris(payeeID: string, extras?: NavigationExtras) {
    this.router.navigate(['ebills', { payeeID }], extras || this.defaultRelative);
  }

  // External Navigations
  navigateToRecurringPayment(id: string) {
    this.communicationService?.navigateToRecurringPayment(id);
  }
  navigateToOneOffPayment(id: string) {
    this.communicationService?.navigateToOneOffPayment(id);
  }
  navigateToPendingPayments() {
    this.communicationService?.navigateToPaymentsList();
  }
}
