import { ChangeDetectorRef, Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PostElectronicRequestPayee } from '@backbase/data-ang/billpay';
import { Observable, of } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';

import { BillpayManagePayeeStateService } from '../../services/billpay-manage-payee-state.service';
import { ManagePayeesNavigationService } from '../../services/manage-payees-navigation.service';
import { BillpayManagePayeeDataService } from '../../services/billpay-manage-payee-data.service';
import { BillpayDiscardChangesModalService } from '@backbase/billpay-journeys-common';
import { ElectronicPayeeForm } from '../../model';
import { BaseElectronicPayeePageDirective } from '../../model/base-electronic-payee-page.model';

@Component({
  templateUrl: './billpay-edit-electronic-payee-view.component.html',
  providers: [BillpayDiscardChangesModalService],
})
export class BillpayEditElectronicPayeeViewComponent extends BaseElectronicPayeePageDirective {
  readonly payee$: Observable<ElectronicPayeeForm | undefined>;
  hasLoadingError = false;

  /** @internal */
  constructor(
    route: ActivatedRoute,
    stateService: BillpayManagePayeeStateService,
    discardModalService: BillpayDiscardChangesModalService,
    private readonly router: ManagePayeesNavigationService,
    private readonly dataService: BillpayManagePayeeDataService,
    private readonly cd: ChangeDetectorRef,
  ) {
    super(route, stateService, discardModalService);
    this.payee$ = this.initializePayee();
  }

  onFormSubmitted(payee: PostElectronicRequestPayee) {
    this.stateService.setElectronicPayee(payee);
    this.id$.subscribe(id => this.router.navigateToEditElectronicPayeeReview(id));
  }

  private initializePayee(): Observable<ElectronicPayeeForm | undefined> {
    const payee$ = this.id$.pipe(
      switchMap(id => this.dataService.getElectronicPayee(id)),
      map(payee => this.stateService.electronicGetResponseToForm(payee)),
      catchError(() => {
        this.hasLoadingError = true;
        this.cd.detectChanges();
        return of(undefined);
      }),
    );

    return this.payeeState$.pipe(switchMap(state => (state ? of(state) : payee$)));
  }

  onFormCanceled() {
    this.router.navigateToPayeeList();
  }
}
