import { Component, HostListener, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BillpayManagePayeeStateService } from '../../services/billpay-manage-payee-state.service';
import { switchMap, map, catchError, tap } from 'rxjs/operators';
import { of, Observable, combineLatest } from 'rxjs';
import { ManualPayeeFormState } from '../../model';
import {
  BillpayDiscardChangesFormComponent,
  BillpayDiscardChangesModalService,
} from '@backbase/billpay-journeys-common';
import { ManagePayeesNavigationService } from '../../services/manage-payees-navigation.service';
import { BillpayManagePayeeDataService } from '../../services/billpay-manage-payee-data.service';

@Component({
  templateUrl: './billpay-edit-manual-payee-view.component.html',
  providers: [BillpayDiscardChangesModalService],
})
export class BillpayEditManualPayeeViewComponent implements OnInit, BillpayDiscardChangesFormComponent {
  payeeFormState?: Observable<ManualPayeeFormState>;
  hasLoadingError = false;
  isFormTouched = false;
  private payeeId = '';

  /** @internal */
  constructor(
    private readonly route: ActivatedRoute,
    private readonly stateService: BillpayManagePayeeStateService,
    private readonly dataService: BillpayManagePayeeDataService,
    private readonly router: ManagePayeesNavigationService,
    private readonly discardModalService: BillpayDiscardChangesModalService,
  ) {}

  ngOnInit() {
    this.payeeFormState = combineLatest([
      this.route.paramMap.pipe(map(params => params.get('id') || '')),
      this.stateService.manualPayeeFormState,
    ]).pipe(
      tap(([id]) => (this.payeeId = id)),
      switchMap(
        ([id, state]) =>
          (state && of(state)) ||
          this.dataService.getManualPayee(id).pipe(
            map(payee => payee && this.stateService.toManualFormState(payee)),
            catchError(() => {
              this.hasLoadingError = true;
              return of({} as ManualPayeeFormState);
            }),
          ),
      ),
    );
  }

  get isDiscardModalOpen() {
    return this.discardModalService.isDiscardModalOpen;
  }

  @HostListener('window:beforeunload', ['$event'])
  onWindowUnload($event: BeforeUnloadEvent) {
    if ((this.isFormTouched || this.stateService.isFormProcessing) && !this.isDiscardModalOpen) {
      $event.returnValue = true;
    }
  }

  onFormSubmitted(payee: ManualPayeeFormState) {
    this.stateService.isFormProcessing = true;
    this.stateService.setManualPayee(payee);
    this.router.navigateToEditManualReview(this.payeeId);
  }

  onFormValueChanged() {
    this.isFormTouched = true;
  }

  onFormCanceled() {
    this.router.navigateToPayeeList();
  }

  openModal(): Observable<boolean> {
    return this.isFormTouched || this.stateService.isFormProcessing ? this.discardModalService.openModal() : of(true);
  }

  onModalResolve(resolvedWith: boolean) {
    this.discardModalService.onModalResolve(resolvedWith);
    if (resolvedWith) {
      this.router.navigateToPayeeList();
    }
  }
}
