import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'bb-billpay-payee-stepper',
  templateUrl: './billpay-payee-stepper.component.html',
})
export class BillpayPayeeStepperComponent {
  /**
   * Index of the payee that the stepper is accessing.
   */
  @Input() index: number | undefined;
  /**
   * Total number of payees available to step through.
   */
  @Input() size: number | undefined;

  @Output() nextPayee = new EventEmitter();
  @Output() previousPayee = new EventEmitter();

  handleGetPreviousPayee() {
    this.previousPayee.emit();
  }

  handleGetNextPayee() {
    this.nextPayee.emit();
  }
}
