import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { EbillCredential } from '@backbase/data-ang/billpay';
import { PayverisLogin, PayverisEnrolmentComponentState } from '../../../model';
declare const $localize: any;

@Component({
  selector: 'bb-billpay-payveris-ebills-enrolment-login',
  templateUrl: './billpay-payveris-ebills-enrolment-login.component.html',
})
export class BillpayPayverisEbillsEnrolmentLoginComponent implements OnInit {
  loginForm!: FormGroup;

  /**
   * Name of the payee.
   */
  @Input() payeeName: string | undefined;
  /**
   * Array of login credential objects to be used for username and password elements.
   */
  @Input() credentialsArray: EbillCredential[] | undefined;
  /**
   * The current state of the component. Controls loaded elements.
   */
  @Input() componentState = PayverisEnrolmentComponentState.Undefined;
  /**
   * Displays elements for a failed submission. Defaults to false.
   */
  @Input() credentialsFailed = false;
  @Output() returnToPayeeList = new EventEmitter();
  @Output() payeeLogin = new EventEmitter<PayverisLogin>();

  submitted = false;
  cancelModalOpen = false;
  readonly ComponentStates = PayverisEnrolmentComponentState;

  /** @internal */
  constructor(private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.credentialsArray = this.credentialsArray?.map(credentials => {
      return {
        ...credentials,
        additions: {
          ...credentials.additions,
          label_text: $localize`${credentials.label}`,
        },
      };
    });
    this.loginForm = this.formBuilder.group(this.formFields());
  }

  formFields() {
    const group: any = {};
    this.credentialsArray?.forEach((field, i) => {
      group[i] = [field.value || '', Validators.required];
    });
    return group;
  }

  onPayverisLoginSubmit() {
    this.submitted = true;
    if (this.loginForm.valid) {
      this.payeeLogin.emit(this.loginForm.value);
    }
  }

  onModalResolve(operationCancelled: boolean) {
    this.cancelModalOpen = false;
    if (operationCancelled) {
      this.handleReturnToPayeeList();
    }
  }

  handleReturnToPayeeList() {
    this.returnToPayeeList.emit();
  }
}
