import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';
import { Observable, Subject } from 'rxjs';
import { catchError, pluck } from 'rxjs/operators';
import {
  BillPayPayverisService,
  BillPayPayeesService,
  EbillAccount,
  EbillBiller,
  EbillCredential,
  OtpDeliveryMethod,
  BillPayPayverisEbillsEnrolmentPostRequestBody,
  BillPayPayverisEbillsEnrolmentPostResponseBody,
} from '@backbase/data-ang/billpay';
import { BillpayUtilityService as Utilities } from '@backbase/billpay-journeys-common';
import { PayverisEnrolmentState } from '../model/payveris-enrolment-state.enum';
import { PayverisEnrolmentComponentState, PayverisOtpPasscode, PayverisLogin, PayverisCaptcha } from '../model';

@Injectable()
export class BillpayPayverisEbillsEnrolmentWidgetService {
  private readonly widgetStateSource = new Subject<void>();
  private _payeeId: string | undefined;
  private _currentState = PayverisEnrolmentState.Initialize;
  private _errorState: string | undefined;
  private _componentState = PayverisEnrolmentComponentState.Undefined;
  private _userInputFailed = false;
  private readonly _responses = <any>{};
  private _expectingLoginResponse = true;
  private _otpTransportMethodSelected: string | undefined;
  private _expectingOtpEntryResponse = false;
  private _payload: BillPayPayverisEbillsEnrolmentPostRequestBody;
  private readonly _MIN_CALLBACK_TIME = 1;
  private timer: ReturnType<typeof setTimeout> | undefined;

  readonly widgetStateChange: Observable<void> = this.widgetStateSource.asObservable();

  /** @internal */
  constructor(
    private readonly payverisData: BillPayPayverisService,
    private readonly payeesData: BillPayPayeesService,
  ) {
    this._payload = {
      payeeID: <string>this._payeeId,
      credentials: [],
    };
  }

  submitPayverisRequest(
    payload: BillPayPayverisEbillsEnrolmentPostRequestBody,
  ): Observable<HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>> {
    this._componentState = PayverisEnrolmentComponentState.Checking;
    this._userInputFailed = false;
    return this.payverisData
      .postBillPayPayverisEbillsEnrolment({ billPayPayverisEbillsEnrolmentPostRequestBody: payload }, 'response')
      .pipe(
        catchError(err => {
          this.handleError(err);
          return new Observable<HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>>();
        }),
      );
  }

  getPayeeNameFromID(id: string | undefined): Observable<string> {
    return this.payeesData.getElectronicPayeeById({ id: <string>id }).pipe(pluck('payee', 'name'));
  }

  onInitializeEnrolment() {
    if (this._payeeId) {
      this._payload = {
        payeeID: this._payeeId,
        credentials: [],
      };
    } else {
      this.handleError(undefined);
      return false;
    }
    this.submitPayverisRequest(this._payload).subscribe(response => {
      this.handleStatusResponse(response);
    });
    return true;
  }

  private handleStatusResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>) {
    if (this.isBillersResponse(response)) {
      return this.setState(PayverisEnrolmentState.SelectBiller, response, 'billersResponse');
    }
    if (this.isSelectAccountResponse(response)) {
      return this.setState(PayverisEnrolmentState.SelectAccounts, response, 'accountsResponse');
    }
    if (this.isOtpSelectResponse(response)) {
      return this.setState(PayverisEnrolmentState.OtpSelect, response, 'otpTransportResponse');
    }
    if (this.isCaptchaResponse(response)) {
      return this.setState(PayverisEnrolmentState.Captcha, response, 'captchaResponse');
    }
    if (this.isCredentialsResponse(response)) {
      return this.handleCredentialsOnStatusResponse(response);
    }
    if (this.isCallbackResponse(response)) {
      return this.handleCallbackResponse(response);
    }
    if (this.isSuccessResponse(response)) {
      return this.setState(PayverisEnrolmentState.Success);
    }
    this.handleError(undefined);
    return false;
  }

  private handleCredentialsOnStatusResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>) {
    if (this._expectingLoginResponse) {
      this._expectingLoginResponse = false;
      return this.setState(PayverisEnrolmentState.Login, response, 'credentialsResponse');
    }
    if (this._expectingOtpEntryResponse) {
      this._expectingOtpEntryResponse = false;
      return this.setState(PayverisEnrolmentState.OtpEntry, response, 'otpPasscodeResponse');
    }
    return this.setState(PayverisEnrolmentState.Mfa, response, 'mfaResponse');
  }

  onSubmitBillerSelection(billerSelectedIndex: number) {
    const billerSelected = this.billers ? this.billers[billerSelectedIndex] : {};
    this._payload = {
      payeeID: <string>this._payeeId,
      biller: billerSelected,
      credentials: [],
    };
    this.submitPayverisRequest(this._payload).subscribe(response => {
      this.handleBillerResponse(response);
    });
  }

  private handleBillerResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>) {
    if (this.isCredentialsResponse(response)) {
      return this.setState(PayverisEnrolmentState.Login, response, 'credentialsResponse');
    }
    if (this.isCallbackResponse(response)) {
      return this.handleCallbackResponse(response);
    }
    this.handleError(undefined);
    return false;
  }

  onSubmitPayeeLogin(login: PayverisLogin | undefined) {
    if (login && this.loginCredentialsArray && this.loginCredentialsArray.length >= 2) {
      const credentials = this.loginCredentialsArray?.map((credentials, i) => {
        return {
          ...credentials,
          value: login[i],
        };
      });
      this._payload = {
        payeeID: <string>this._payeeId,
        credentials: credentials,
      };
    } else {
      this.handleError(undefined);
    }
    this.submitPayverisRequest(this._payload).subscribe(response => {
      this.handleCredentialResponse(response);
    });
  }

  private handleCredentialResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>): boolean {
    if (this.isOtpSelectResponse(response)) {
      return this.setState(PayverisEnrolmentState.OtpSelect, response, 'otpTransportResponse');
    }
    if (this.isCaptchaResponse(response)) {
      return this.setState(PayverisEnrolmentState.Captcha, response, 'captchaResponse');
    }
    if (this.isCredentialsResponse(response)) {
      return this.setState(PayverisEnrolmentState.Mfa, response, 'mfaResponse');
    }
    if (this.isSelectAccountResponse(response)) {
      return this.setState(PayverisEnrolmentState.SelectAccounts, response, 'accountsResponse');
    }
    if (this.isCallbackResponse(response)) {
      return this.handleCallbackResponse(response);
    }
    this.handleError(undefined);
    return false;
  }

  onSubmitPayverisEbillMfa() {
    this._payload = {
      payeeID: <string>this._payeeId,
      credentials: this.mfaArray || [],
    };
    this.submitPayverisRequest(this._payload).subscribe(response => {
      this.handleMfaResponse(response);
    });
  }

  private handleMfaResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>): boolean {
    if (this.isCredentialsResponse(response)) {
      return this.setState(PayverisEnrolmentState.Mfa, response, 'mfaResponse');
    }
    if (this.isSelectAccountResponse(response)) {
      return this.setState(PayverisEnrolmentState.SelectAccounts, response, 'accountsResponse');
    }
    if (this.isCallbackResponse(response)) {
      return this.handleCallbackResponse(response);
    }
    this.handleError(undefined);
    return false;
  }

  onSubmitPayverisEbillOtpTransportSelection(transportSelectedIndex: number) {
    this._expectingOtpEntryResponse = true;
    const transportSelected = this.otpTransports ? this.otpTransports[transportSelectedIndex] : {};
    this._otpTransportMethodSelected = transportSelected.value;
    const label = this.getLabelFromResponse(this._responses['otpTransportResponse']);
    const selection: EbillCredential = {
      label: label,
      value: transportSelected.id,
    };
    this._payload = {
      payeeID: <string>this._payeeId,
      credentials: [selection],
    };
    this.submitPayverisRequest(this._payload).subscribe(response => {
      this.handleOtpTransportResponse(response);
    });
  }

  private handleOtpTransportResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>): boolean {
    if (this.isCredentialsResponse(response)) {
      return this.setState(PayverisEnrolmentState.OtpEntry, response, 'otpPasscodeResponse');
    }
    if (this.isCallbackResponse(response)) {
      return this.handleCallbackResponse(response);
    }
    this.handleError(undefined);
    return false;
  }

  onSubmitPayverisEbillPayeeOtpPasscode(passcode: PayverisOtpPasscode) {
    this.submitVerificationStep(this._responses['otpPasscodeResponse'], passcode.passcode);
  }

  onSubmitPayverisEbillPayeeCaptcha(captcha: PayverisCaptcha) {
    this.submitVerificationStep(this._responses['captchaResponse'], captcha.captcha);
  }

  private submitVerificationStep(
    previousResponse: BillPayPayverisEbillsEnrolmentPostResponseBody | undefined,
    credentialValue: string | undefined,
  ) {
    this._payload = this.prepareCredentialPayload(previousResponse, credentialValue);
    this.submitPayverisRequest(this._payload).subscribe(response => {
      this.handleVerificationResponse(response);
    });
  }

  private prepareCredentialPayload(
    response: BillPayPayverisEbillsEnrolmentPostResponseBody | undefined,
    credentialValue: string | undefined,
  ): BillPayPayverisEbillsEnrolmentPostRequestBody {
    const label = this.getLabelFromResponse(response);
    const selection: EbillCredential = {
      label: label,
      value: credentialValue,
    };
    return {
      payeeID: <string>this.payeeId,
      credentials: [selection],
    };
  }

  private handleVerificationResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>) {
    if (this.isSelectAccountResponse(response)) {
      return this.setState(PayverisEnrolmentState.SelectAccounts, response, 'accountsResponse');
    }
    if (this.isCallbackResponse(response)) {
      return this.handleCallbackResponse(response);
    }
    this.handleError(undefined);
    return false;
  }

  cancelEnrolment(): Observable<HttpResponse<any>> {
    return this.payverisData.deleteBillPayPayverisEbillsEnrolment(this._payload);
  }

  onSubmitPayverisEbillAccountSelection(accountSelectedIndex: number) {
    const accountSelected = this.accounts ? this.accounts[accountSelectedIndex] : undefined;
    this._payload = {
      payeeID: <string>this._payeeId,
      account: accountSelected,
      credentials: [],
    };
    this.submitPayverisRequest(this._payload).subscribe(response => {
      this.handleAccountsResponse(response);
    });
  }

  private handleAccountsResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>) {
    if (this.isCallbackResponse(response)) {
      return this.handleCallbackResponse(response);
    }
    if (this.isSuccessResponse(response)) {
      return this.setState(PayverisEnrolmentState.Success, response, 'successResponse');
    }
    this.handleError(undefined);
    return false;
  }

  private handleCallbackResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>) {
    const callbackTime = Utilities.checkForProperty('body.callBackTime', response) || 0;
    const callbackTimeInMs = Math.max(new Date(callbackTime).getTime() - new Date().getTime(), this._MIN_CALLBACK_TIME);
    this.timer = setTimeout(() => {
      this.onInitializeEnrolment();
    }, callbackTimeInMs);
    this._componentState = PayverisEnrolmentComponentState.Callback;
    this.widgetStateSource.next();
    return true;
  }

  cancelCallback() {
    if (this.timer) {
      clearTimeout(this.timer);
    }
  }

  private setState(
    state: PayverisEnrolmentState,
    response?: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody> | undefined,
    responseToSet?: string | undefined,
  ) {
    this._currentState = state;
    this._componentState = PayverisEnrolmentComponentState.Ready;
    if (responseToSet) {
      this._responses[responseToSet] = Utilities.checkForProperty('body', response);
    }
    this.widgetStateSource.next();
    return true;
  }

  private handleError(error: any) {
    this._errorState = Utilities.checkForProperty(['error', 'errors', 0, 'key'], error);
    if (this._errorState === 'billpay.api.invalidCredentials') {
      this._userInputFailed = true;
      this._componentState = PayverisEnrolmentComponentState.Ready;
      this.widgetStateSource.next();
    } else {
      this.setState(PayverisEnrolmentState.ErrorState);
    }
  }

  private isCredentialsResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>): boolean {
    return !!(
      response.status === 200 && Utilities.checkForProperty('body.authentication.credentials.length', response) > 0
    );
  }

  private isBillersResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>): boolean {
    return !!(response.status === 200 && Utilities.checkForProperty('body.billers.length', response) > 0);
  }

  private isCallbackResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>): boolean {
    const accounts = Utilities.checkForProperty('body.accounts', response);
    return !!(
      response.status === 200 &&
      (!accounts || accounts.length === 0) &&
      Utilities.checkForProperty('body.callBackRequired', response) &&
      Utilities.checkForProperty('body.callBackTime', response)
    );
  }

  private isSelectAccountResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>): boolean {
    return !!(response.status === 200 && Utilities.checkForProperty('body.accounts.length', response) >= 1);
  }

  private isOtpSelectResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>): boolean {
    return !!(
      response.status === 200 &&
      Utilities.checkForProperty('body.authentication.securityChallenge.oneTimePasscode', response)
    );
  }

  private isCaptchaResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>): boolean {
    return !!(
      response.status === 200 &&
      Utilities.checkForProperty('body.authentication.securityChallenge.captcha', response) &&
      Utilities.checkForProperty('body.authentication.credentials', response)
    );
  }

  private isSuccessResponse(response: HttpResponse<BillPayPayverisEbillsEnrolmentPostResponseBody>): boolean {
    return !!(response.status === 200 && Utilities.checkForProperty('body.payeeID', response));
  }

  private getLabelFromResponse(
    response: BillPayPayverisEbillsEnrolmentPostResponseBody | undefined,
  ): string | undefined {
    return Utilities.checkForProperty(['authentication', 'credentials', 0, 'label'], response);
  }

  set payeeId(value: string | undefined) {
    this._payeeId = value;
  }

  credentialsResponse = (value: BillPayPayverisEbillsEnrolmentPostResponseBody | undefined) => {
    this._responses.credentialsResponse = value;
  };
  get payeeId(): string | undefined {
    return this._payeeId;
  }

  get currentState(): PayverisEnrolmentState {
    return this._currentState;
  }

  get errorState(): string | undefined {
    return this._errorState;
  }

  get componentState(): PayverisEnrolmentComponentState {
    return this._componentState;
  }

  set componentState(value: PayverisEnrolmentComponentState) {
    this._componentState = value;
  }

  get userInputFailed(): boolean {
    return this._userInputFailed;
  }

  get loginCredentialsArray(): EbillCredential[] | undefined {
    return Utilities.checkForProperty('credentialsResponse.authentication.credentials', this._responses) || [];
  }

  get mfaArray(): EbillCredential[] | undefined {
    return Utilities.checkForProperty('mfaResponse.authentication.credentials', this._responses) || [];
  }

  get billers(): EbillBiller[] {
    return Utilities.checkForProperty('billersResponse.billers', this._responses) || [];
  }

  get accounts(): EbillAccount[] {
    return Utilities.checkForProperty('accountsResponse.accounts', this._responses) || [];
  }

  get otpTransportMethodSelected(): string | undefined {
    return this._otpTransportMethodSelected;
  }

  private checkForPropertyWrapper(propertyBranch: string): any | undefined {
    return Utilities.checkForProperty(propertyBranch, this._responses);
  }

  get otpTransports(): OtpDeliveryMethod[] | undefined {
    return this.checkForPropertyWrapper(
      'otpTransportResponse.authentication.securityChallenge.oneTimePasscode.deliveryMethods',
    );
  }

  get captchaImageSrc(): string | undefined {
    return this.checkForPropertyWrapper('captchaResponse.authentication.securityChallenge.captcha.image');
  }

  get captchaImageType(): string | undefined {
    return this.checkForPropertyWrapper('captchaResponse.authentication.securityChallenge.captcha.mimeType');
  }
}
