import { ManagePayeesJourneyConfigService } from '../../services/manage-payees-journey-config.service';
import { Component, TemplateRef, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { BillpayDiscardChangesModalService } from '@backbase/billpay-journeys-common';
import { ElectronicPayeeForm } from '../../model';
import { BillpayManagePayeeStateService } from '../../services/billpay-manage-payee-state.service';
import { ManagePayeesNavigationService } from '../../services/manage-payees-navigation.service';
import { BillpayManagePayeeDataService } from '../../services/billpay-manage-payee-data.service';
import { BaseElectronicPayeeReviewPageModelDirective } from '../../directives/base-electronic-payee-review-page.model';
import { NotificationService } from '@backbase/ui-ang/notification';

@Component({
  templateUrl: './billpay-edit-electronic-payee-review-view.component.html',
  providers: [BillpayDiscardChangesModalService],
})
export class EditElectronicPayeeReviewViewComponent extends BaseElectronicPayeeReviewPageModelDirective {
  @ViewChild('notificationHeader') notificationHeaderTemplate: TemplateRef<any> | string = '';
  @ViewChild('notificationMessage') notificationMessageTemplate: TemplateRef<any> | string = '';

  protected readonly payeeSubmit$ = new BehaviorSubject<ElectronicPayeeForm | undefined>(undefined);

  /** @internal */
  constructor(
    readonly config: ManagePayeesJourneyConfigService,
    route: ActivatedRoute,
    stateService: BillpayManagePayeeStateService,
    discardModalService: BillpayDiscardChangesModalService,
    private readonly router: ManagePayeesNavigationService,
    private readonly dataService: BillpayManagePayeeDataService,
    private readonly notificationService: NotificationService,
  ) {
    super(route, stateService, discardModalService, config);
  }

  submitPayee(payee: ElectronicPayeeForm) {
    this.payeeSubmit$.next(payee);
  }

  navigateBack(id: string) {
    this.router.navigateToEditElectronicPayeeForm(id);
  }

  protected onSubmitPayee(payee: ElectronicPayeeForm) {
    this.id$.pipe(switchMap(id => this.dataService.updateElectronicPayee(id, payee))).subscribe(
      () => {
        this.router.navigateToPayeeList();
        this.notificationService.showNotification({
          header: this.notificationHeaderTemplate,
          message: this.notificationMessageTemplate,
          modifier: 'success',
          ttl: this.dismissTimeout,
        });
      },
      () => {
        this.notificationService.showNotification({
          header: $localize`:@@billpay.edit-e-payee.error-notification.header:Something went wrong`,
          message: $localize`:@@billpay.edit-e-payee.error-notification.message:We were unable to proceed. Please try again.`,
          modifier: 'error',
          ttl: this.dismissTimeout,
        });
      },
    );
  }

  cancel() {
    this.router.navigateToPayeeList();
  }
}
