import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { combineLatest, Observable, of } from 'rxjs';
import { map, switchMap, take } from 'rxjs/operators';

import { BillpayManagePayeeStateService } from '../../services/billpay-manage-payee-state.service';
import { ManagePayeesNavigationService } from '../../services/manage-payees-navigation.service';
import { ElectronicPayeeForm } from '../../model';
import { BaseElectronicPayeePageDirective } from '../../model/base-electronic-payee-page.model';
import { BillpayDiscardChangesModalService } from '@backbase/billpay-journeys-common';

@Component({
  templateUrl: './billpay-create-electronic-payee-view.component.html',
  providers: [BillpayDiscardChangesModalService],
})
export class BillpayCreateElectronicPayeeViewComponent extends BaseElectronicPayeePageDirective {
  readonly payee$: Observable<ElectronicPayeeForm>;
  readonly zipRequired$: Observable<boolean>;
  private readonly name$: Observable<string>;

  /** @internal */
  constructor(
    route: ActivatedRoute,
    stateService: BillpayManagePayeeStateService,
    discardModalService: BillpayDiscardChangesModalService,
    private readonly router: ManagePayeesNavigationService,
  ) {
    super(route, stateService, discardModalService);

    this.zipRequired$ = this.route.queryParamMap.pipe(
      take(1),
      map(params => params.get('zipRequired')),
      map(zipRequired => (zipRequired ? JSON.parse(zipRequired) : false)),
    );
    this.name$ = this.route.queryParamMap.pipe(
      take(1),
      map(params => params.get('name') || ''),
    );
    this.payee$ = this.initializePayee();
  }

  onFormSubmitted(payee: ElectronicPayeeForm) {
    this.stateService.setElectronicPayee(payee);
    this.id$.subscribe(id => this.router.navigateToElectronicPayeeReview(id, { queryParamsHandling: 'preserve' }));
  }

  onBackToSearch() {
    this.isFindPayeeModal = true;
    this.router.navigateToSearch();
  }

  onFormCanceled() {
    this.router.navigateToPayeeList();
  }

  private initializePayee(): Observable<ElectronicPayeeForm> {
    const payee$ = combineLatest([this.zipRequired$, this.name$]).pipe(
      map(([zipRequired, name]) => ({
        name,
        accountNumber: '',
        nickName: '',
        ...(zipRequired && { merchantZipCode: '' }),
      })),
    );

    return this.payeeState$.pipe(switchMap(state => (state ? of(state) : payee$)));
  }
}
