import { ManagePayeesJourneyConfigService } from '../../services/manage-payees-journey-config.service';
import { Component, HostListener, OnDestroy, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { filter, switchMap, take, takeUntil, tap } from 'rxjs/operators';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import {
  BillpayDiscardChangesModalService,
  BillpayDiscardChangesFormComponent,
  BillpayNotificationRoutingService,
} from '@backbase/billpay-journeys-common';
import { ManualPayeeFormState } from '../../model';
import { ManagePayeesNavigationService } from '../../services/manage-payees-navigation.service';
import { BillpayManagePayeeStateService } from '../../services/billpay-manage-payee-state.service';
import { BillpayManagePayeeDataService } from '../../services/billpay-manage-payee-data.service';
import { NotificationService } from '@backbase/ui-ang/notification';

declare const $localize: any;
@Component({
  templateUrl: './billpay-create-manual-payee-review-view.component.html',
  providers: [BillpayDiscardChangesModalService],
})
export class CreateManualPayeeReviewViewComponent implements OnInit, OnDestroy, BillpayDiscardChangesFormComponent {
  @ViewChild('notificationHeader') notificationHeaderTemplate: TemplateRef<any> | string = '';
  @ViewChild('notificationMessage') notificationMessageTemplate: TemplateRef<any> | string = '';
  payee?: ManualPayeeFormState;
  private readonly payeeSubmit$ = new BehaviorSubject<ManualPayeeFormState | undefined>(undefined);
  private readonly destroy$ = new Subject<void>();

  /** @internal */
  constructor(
    readonly config: ManagePayeesJourneyConfigService,
    private readonly router: ManagePayeesNavigationService,
    private readonly stateService: BillpayManagePayeeStateService,
    private readonly dataService: BillpayManagePayeeDataService,
    private readonly notificationService: NotificationService,
    private readonly discardModalService: BillpayDiscardChangesModalService,
    private readonly billpayNotificationRoutingService: BillpayNotificationRoutingService,
  ) {}

  get isDiscardModalOpen() {
    return this.discardModalService.isDiscardModalOpen;
  }

  ngOnInit() {
    this.stateService.manualPayeeFormState.pipe(take(1)).subscribe(payee => {
      this.payee = payee;
      if (!this.payee) {
        this.router.navigateToManualPayeeForm();
      }
    });
    this.payeeSubmit$
      .pipe(takeUntil(this.destroy$), filter(Boolean))
      .subscribe(payee => this.onSubmitPayee(payee as ManualPayeeFormState));
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  @HostListener('window:beforeunload', ['$event'])
  onWindowUnload($event: BeforeUnloadEvent) {
    if (!this.isDiscardModalOpen) {
      $event.returnValue = true;
    }
  }

  submitPayee(payee: ManualPayeeFormState) {
    this.payeeSubmit$.next(payee);
  }

  navigateBack() {
    this.router.navigateToManualPayeeForm();
  }

  cancel() {
    this.router.navigateToPayeeList();
  }

  navigateToPayee() {
    this.billpayNotificationRoutingService.navigateToPayees();
  }

  openModal(): Observable<boolean> {
    return this.payeeSubmit$.pipe(
      switchMap(payee => (payee ? of(true) : this.discardModalService.openModal())),
      tap(isResolved => isResolved && this.stateService.resetManualPayee()),
    );
  }

  onModalResolve(resolvedWith: boolean) {
    this.discardModalService.onModalResolve(resolvedWith);
  }

  private onSubmitPayee(payee: ManualPayeeFormState) {
    this.dataService.addManualPayee(payee).subscribe(
      () => {
        this.router.navigateToPayeeList();
        this.notificationService.showNotification({
          header: this.notificationHeaderTemplate,
          message: this.notificationMessageTemplate,
          modifier: 'success',
          ttl: this.dismissTimeout,
        });
      },
      () =>
        this.notificationService.showNotification({
          header: $localize`:@@billpay.create-manuel-payee.error-notification.header:Something went wrong`,
          message: $localize`:@@billpay.create-manuel-payee.error-notification.header:We were unable to proceed. Please try again.`,
          modifier: 'error',
          ttl: this.dismissTimeout,
        }),
    );
  }

  private get dismissTimeout(): number {
    if (this.config.notificationDismissTime === undefined) {
      throw new Error('Dismiss timeout value not found');
    }
    return this.config.notificationDismissTime;
  }
}
