import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormArray, FormControl, FormGroup, Validators } from '@angular/forms';
import { EbillCredential } from '@backbase/data-ang/billpay';
import { BillpayUtilityService as Utilities } from '@backbase/billpay-journeys-common';
import { PayverisEnrolmentComponentState } from '../../../model';

@Component({
  selector: 'bb-billpay-payveris-ebills-enrolment-mfa-entry',
  templateUrl: './billpay-payveris-ebills-enrolment-mfa-entry.component.html',
})
export class BillpayPayverisEbillsEnrolmentMfaEntryComponent implements OnInit {
  submitted = false;
  cancelModalOpen = false;
  mfaLabel: string | undefined = undefined;
  readonly ComponentStates = PayverisEnrolmentComponentState;

  /**
   * Name of the payee.
   */
  @Input() payeeName: string | undefined;
  /**
   * Array of MFA options.
   */
  @Input() mfaArray: EbillCredential[] | undefined;
  /**
   * The current state of the component. Controls loaded elements.
   */
  @Input() componentState = PayverisEnrolmentComponentState.Undefined;
  /**
   * Displays elements for a failed submission. Defaults to false.
   */
  @Input() mfaFailed = false;
  @Output() returnToPayeeList = new EventEmitter();
  @Output() mfaEntry = new EventEmitter();

  mfaForm = new FormGroup({
    formQuestions: new FormArray([]),
  });

  get formQuestions() {
    return this.mfaForm.get('formQuestions') as FormArray;
  }

  ngOnInit() {
    if (this.mfaArray) {
      this.mfaArray.forEach(() => {
        this.formQuestions.push(new FormControl('', Validators.required));
      });
    }
    this.mfaLabel = Utilities.checkForProperty([0, 'label'], this.mfaArray);
  }

  onMfaSubmit() {
    this.submitted = true;
    if (this.mfaForm.valid) {
      this.mfaEntry.emit();
      this.submitted = false;
    }
  }

  onModalResolve(operationCancelled: boolean) {
    this.cancelModalOpen = false;
    if (operationCancelled) {
      this.handleReturnToPayeeList();
    }
  }

  handleReturnToPayeeList() {
    this.returnToPayeeList.emit();
  }
}
