import { Component, Input, OnInit, TemplateRef } from '@angular/core';
import { NotificationService } from '@backbase/ui-ang/notification';

@Component({
  selector: 'bb-billpay-payveris-ebills-enrolment-notification-success',
  template: '',
})
export class BillpayPayverisEbillsEnrolmentNotificationSuccessComponent implements OnInit {
  /**
   * Time until the notification disappears in milliseconds.
   */
  @Input() ttl = 5000;
  /**
   * The modal header.
   */
  @Input() header: string | TemplateRef<any> | undefined;
  /**
   * The modal message.
   */
  @Input() message: string | TemplateRef<any> = '';

  /** @internal */
  constructor(private readonly notificationService: NotificationService) {}

  ngOnInit() {
    this.notificationService.showNotification({
      header: this.header,
      message: this.message,
      dismissible: true,
      modifier: 'success',
      ttl: this.ttl,
    });
  }
}
