import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Injectable } from '@angular/core';
import { BillpayBaseDeactivateGuardService } from '@backbase/billpay-journeys-common';

@Injectable()
export class BillpayManagePayeeDeactivateGuardService extends BillpayBaseDeactivateGuardService {
  protected readonly allowedPreviousRoutes: RegExp[] = [
    /^.*\/manual(\/review)?$/,
    /^.*\/manual\/edit\/.+(\/review.+)?$/,
    /^.*\/electronic\/.+(\/review.+)?$/,
    /^.*\/electronic\/edit\/.+(\/review.+)?$/,
  ];
  protected readonly allowedNextRoutes: RegExp[] = [
    /^.*\/manual(\/review)?$/,
    /^.*\/manual\/edit\/.+(\/review.+)?$/,
    /^.*\/electronic\/.+(\/review.+)?$/,
    /^.*\/electronic\/edit\/.+(\/review.+)?$/,
  ];

  /** @internal */
  constructor(router: Router, location: Location) {
    super(router, location);
  }
}
