import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { PayverisCaptcha, PayverisEnrolmentComponentState } from '../../../model';
import { take } from 'rxjs/operators';

@Component({
  selector: 'bb-billpay-payveris-ebills-enrolment-captcha',
  templateUrl: './billpay-payveris-ebills-enrolment-captcha.component.html',
})
export class BillpayPayverisEbillsEnrolmentCaptchaComponent implements OnInit {
  imageSrcRaw = '';
  imageSrcData: SafeResourceUrl | undefined;
  submitted = false;
  cancelModalOpen = false;
  _captchaFailed = false;
  captchaRecentlyFailed = false;
  readonly ComponentStates = PayverisEnrolmentComponentState;

  /**
   * Name of the payee.
   */
  @Input() payeeName: string | undefined;
  /**
   * Image type of the captcha.
   */
  @Input() captchaImageType: string | undefined;
  /**
   * Image source of the captcha.
   */
  @Input() captchaImageSrc: string | undefined;
  /**
   * The current state of the component. Controls loaded elements.
   */
  @Input() componentState = PayverisEnrolmentComponentState.Undefined;

  @Input() set captchaFailed(hasFailed: boolean) {
    this._captchaFailed = hasFailed;
    this.captchaRecentlyFailed = hasFailed;
    if (hasFailed) {
      this.captchaForm.controls['captcha'].markAsDirty();
      this.captchaForm.controls['captcha'].markAsTouched();
      this.captchaForm.controls['captcha'].setValue('');
      this.captchaForm.controls['captcha'].valueChanges.pipe(take(1)).subscribe(() => {
        this.captchaRecentlyFailed = false;
      });
    }
  }
  @Output() returnToPayeeList = new EventEmitter();
  @Output() captchaEntry = new EventEmitter<PayverisCaptcha>();

  captchaForm = new FormGroup({
    captcha: new FormControl('', Validators.required),
  });

  /** @internal */
  constructor(private readonly sanitizer: DomSanitizer) {}

  ngOnInit() {
    const imageType = this.captchaImageType || '';
    const imageSrc = this.captchaImageSrc || '';
    this.imageSrcRaw = `data:${imageType};base64,${imageSrc}`;
    this.imageSrcData = this.sanitizer.bypassSecurityTrustResourceUrl(this.imageSrcRaw);
  }

  /**
   * @return _captchaFailed. Displays elements for a failed submission. Invalidates captcha form.
   */
  get captchaFailed(): boolean {
    return this._captchaFailed;
  }

  onCaptchaSubmit() {
    this.submitted = true;
    if (this.captchaForm.valid) {
      this.captchaEntry.emit(this.captchaForm.value as PayverisCaptcha);
    }
  }

  onModalResolve(operationCancelled: boolean) {
    this.cancelModalOpen = false;
    if (operationCancelled) {
      this.handleReturnToPayeeList();
    }
  }

  handleReturnToPayeeList() {
    this.returnToPayeeList.emit();
  }
}
