import { ChangeDetectorRef, Component, OnDestroy, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { Observable, of, Subject } from 'rxjs';
import { PayeeSummariesByEbill } from '../../model';
import { skip, switchMap, take, takeUntil } from 'rxjs/operators';
import { HttpResponse } from '@angular/common/http';
import { BillpayNotificationRoutingService, BillpayPayeeDataService } from '@backbase/billpay-journeys-common';
import { ActivatedRoute, NavigationExtras } from '@angular/router';
import { Currency, PayeeSummary } from '@backbase/billpay-http-ang';
import { ManagePayeesJourneyConfigService } from '../../services/manage-payees-journey-config.service';
import { ManagePayeesNavigationService } from '../../services/manage-payees-navigation.service';
import { NotificationService } from '@backbase/ui-ang/notification';

@Component({
  selector: 'bb-billpay-payee-list-view',
  templateUrl: './billpay-payee-list-view.component.html',
})
export class PayeeListViewComponent implements OnDestroy, OnInit {
  private readonly navigationExtras: NavigationExtras;
  @ViewChild('notificationMessage') notificationMessageTemplate: TemplateRef<any> | string = '';
  @ViewChild('notificationHeader') notificationHeaderTemplate: TemplateRef<any> | string = '';
  payeeSummariesByEbillObservable: Observable<PayeeSummariesByEbill | undefined> = new Observable();
  payeeSummariesByEbill: PayeeSummariesByEbill | undefined;
  destroy$ = new Subject<boolean>();
  isDeleteModalOpened = false;
  isDeleteEbillsModalOpen = false;
  isNavigateModalOpened = false;
  notificationType = '';

  /** @internal */
  constructor(
    private readonly routerService: ManagePayeesNavigationService,
    private readonly route: ActivatedRoute,
    readonly config: ManagePayeesJourneyConfigService,
    private readonly payeeDataService: BillpayPayeeDataService,
    private readonly notificationService: NotificationService,
    private readonly cdRef: ChangeDetectorRef,
    private readonly billpayNotificationRoutingService: BillpayNotificationRoutingService,
  ) {
    this.navigationExtras = { relativeTo: this.route };
  }

  ngOnInit() {
    this.payeeSummariesByEbillObservable = this.payeeDataService.getPayees();

    this.setupNavigationSubscriptions();

    this.payeeDataService.selectedDeletePayee
      .pipe(takeUntil(this.destroy$), skip(1))
      .subscribe(payee => (payee ? (this.isDeleteModalOpened = true) : (this.isNavigateModalOpened = true)));

    this.payeeDataService.selectedDeleteEbills
      .pipe(takeUntil(this.destroy$), skip(1))
      .subscribe(payee => (payee ? (this.isDeleteEbillsModalOpen = true) : (this.isNavigateModalOpened = true)));
  }
  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private deletePayee(): Observable<HttpResponse<any> | {}> {
    return this.payeeDataService.selectedDeletePayee.pipe(
      switchMap((payee: any) =>
        payee ? this.payeeDataService.deletePayee(payee.id, payee.electronic) : of<Observable<any>>(),
      ),
    );
  }
  private deleteEbills(): Observable<HttpResponse<PayeeSummary> | {}> {
    return this.payeeDataService.selectedDeleteEbills.pipe(
      switchMap((payee: any) => (payee ? this.payeeDataService.deleteEbills(payee.id) : of<Observable<any>>())),
    );
  }

  private get dismissTimeout(): number {
    if (this.config.notificationDismissTime === undefined) {
      throw new Error('Dismiss timeout value not found');
    }
    return this.config.notificationDismissTime;
  }

  selectedDeletePayee(): Observable<PayeeSummary | undefined> {
    return this.payeeDataService.selectedDeletePayee;
  }

  selectedDeleteEbills(): Observable<PayeeSummary | undefined> {
    return this.payeeDataService.selectedDeleteEbills;
  }

  onNavigateToPayments(hasNavigated: boolean) {
    if (hasNavigated) {
      this.goToPaymentsList();
    }
    this.isNavigateModalOpened = false;
  }

  onDeletePayee(resolve: boolean) {
    this.isDeleteModalOpened = false;

    if (resolve) {
      this.deletePayee()
        .pipe(take(1))
        .subscribe(() => {
          this.deletionNotify('delete');
        });
    }
  }

  onDeleteEbills(resolve: boolean) {
    this.isDeleteEbillsModalOpen = false;

    if (resolve) {
      this.deleteEbills()
        .pipe(take(1))
        .subscribe(() => {
          this.deletionNotify('ebills');
        });
    }
  }

  private deletionNotify(type: string) {
    this.payeeDataService.reloadPayees();
    this.cdRef.markForCheck();
    this.notificationType = type;
    this.notificationService.showNotification({
      header: this.notificationHeaderTemplate,
      message: this.notificationMessageTemplate,
      modifier: 'success',
      ttl: this.dismissTimeout,
    });
  }

  private setupNavigationSubscriptions() {
    this.billpayNotificationRoutingService.managePayeesSubject$
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => this.goToAddPayee());

    this.billpayNotificationRoutingService.managePaymentSubject$
      .pipe(takeUntil(this.destroy$))
      .subscribe(async payeeId =>
        this.gotoOneOffPayment((await this.payeeDataService.findPayeeSummaryById(payeeId))?.id || ''),
      );

    this.billpayNotificationRoutingService.upcommingPaymentsSubject$
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => this.goToPaymentsList());
  }

  areBothListsPresent(payeeSummariesByEbill: PayeeSummariesByEbill): boolean {
    return !!(
      payeeSummariesByEbill.ebillsEnabled &&
      payeeSummariesByEbill.ebillsEnabled.length &&
      payeeSummariesByEbill.ebillsNotEnabled &&
      payeeSummariesByEbill.ebillsNotEnabled.length
    );
  }

  // Get totals of the eBills, grouped by currency
  // Ideally there should only be one currency in use, but it doesn't hurt to check
  getEbillTotals(ebillPayeeSummaries: PayeeSummary[] = []): Currency[] {
    const ebillTotalsByCurrency: Record<string, number> = ebillPayeeSummaries.reduce(
      (currenciesTotalsObj: Record<string, number>, payeeSummary: PayeeSummary) => {
        if (!(payeeSummary.ebill && payeeSummary.ebill.latestBill && payeeSummary.ebill.latestBill.amount)) {
          return currenciesTotalsObj;
        }

        const thisCurrencyCode = payeeSummary.ebill.latestBill.amount.currencyCode;
        const thisAmount = parseFloat(payeeSummary.ebill.latestBill.amount.amount);

        if (!currenciesTotalsObj[thisCurrencyCode]) {
          currenciesTotalsObj[thisCurrencyCode] = 0;
        }
        currenciesTotalsObj[thisCurrencyCode] = currenciesTotalsObj[thisCurrencyCode] + thisAmount;

        return currenciesTotalsObj;
      },
      {},
    );

    return Object.keys(ebillTotalsByCurrency).map(currencyCode => {
      return {
        currencyCode,
        amount: ebillTotalsByCurrency[currencyCode].toString(),
      };
    });
  }

  ngForTrackByCurrencyFn(currencies: Currency) {
    return currencies.currencyCode;
  }

  get currentLoadingState() {
    return this.payeeDataService.payeesLoadingState;
  }

  get areButtonsDisabled(): boolean {
    return this.payeeDataService.areButtonsDisabled;
  }

  get numberOfPayees(): number | undefined {
    return this.payeeDataService.numberOfPayees;
  }

  goToAddPayee() {
    this.routerService.navigateToSearch(this.navigationExtras);
  }
  private goToPaymentsList() {
    this.routerService?.navigateToPendingPayments();
  }
  private gotoOneOffPayment(id: string) {
    this.routerService?.navigateToOneOffPayment(id);
  }
}
