import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { Payee, ElectronicPayeeByIdGetResponseBody } from '@backbase/data-ang/billpay';

import { ManualPayeeFormState, ElectronicPayeeForm } from '../model';

@Injectable()
export class BillpayManagePayeeStateService {
  private readonly manualPayeeStateSource$ = new BehaviorSubject<ManualPayeeFormState | undefined>(undefined);
  readonly manualPayeeFormState: Observable<
    ManualPayeeFormState | undefined
  > = this.manualPayeeStateSource$.asObservable();

  private readonly electronicPayeeStateSource = new BehaviorSubject<ElectronicPayeeForm | undefined>(undefined);
  readonly electronicPayeeFormState: Observable<
    ElectronicPayeeForm | undefined
  > = this.electronicPayeeStateSource.asObservable();

  isFormProcessing = false;

  setManualPayee(state?: ManualPayeeFormState) {
    this.manualPayeeStateSource$.next(state);
  }

  toManualFormState(data: Payee): ManualPayeeFormState {
    return {
      name: data.name,
      address: data.address,
      phoneNumber: data.phoneNumber || '',
      ...(data.accountNumber && { accountNumber: data.accountNumber }),
      ...(data.nickName && { nickName: data.nickName }),
    };
  }

  resetManualPayee() {
    this.setManualPayee();
  }

  setElectronicPayee(state?: ElectronicPayeeForm) {
    this.electronicPayeeStateSource.next(state);
  }

  resetElectronicPayee() {
    this.setElectronicPayee();
  }

  electronicGetResponseToForm(payeeResponse: ElectronicPayeeByIdGetResponseBody): ElectronicPayeeForm {
    const { name, nickName, accountNumber } = payeeResponse.payee;

    return {
      name,
      nickName,
      accountNumber,
    };
  }
}
