import { Component, EventEmitter, Input, Output } from '@angular/core';
import { PayeeSummary, Payee } from '@backbase/data-ang/billpay';
import { PhoneNumberPipe } from '@backbase/ui-ang/phone-number-format-pipe';

@Component({
  selector: 'bb-billpay-payee-information',
  templateUrl: './billpay-payee-information.component.html',
  providers: [PhoneNumberPipe],
})
export class BillpayPayeeInformationComponent {
  /**
   * Details of the payee.
   */
  @Input() payeeSummary: PayeeSummary | undefined;
  /**
   * Full details of the payee for manual payees.
   */
  @Input() fullPayee: Payee | undefined;

  @Output() editPayee = new EventEmitter();
  @Output() deletePayee = new EventEmitter();

  /** @internal */
  constructor(private readonly phoneNumberFormatter: PhoneNumberPipe) {}

  handleEditPayee() {
    this.editPayee.emit();
  }

  handleDeletePayee() {
    this.deletePayee.emit();
  }

  get payeePhoneNumber(): string | undefined {
    return (
      this.fullPayee &&
      this.fullPayee.phoneNumber &&
      this.phoneNumberFormatter.transform(this.fullPayee.phoneNumber, 'US', true)
    );
  }
}
