import { ChangeDetectorRef, Component, OnDestroy, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { ActivatedRoute, NavigationExtras } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { takeUntil, tap } from 'rxjs/operators';
import { Payee, PayeeSummary } from '@backbase/data-ang/billpay';
import { BillpayPayeeSummaryWidgetService } from '../../services/billpay-payee-summary-widget.service';
import { ManagePayeesJourneyConfigService } from '../../services/manage-payees-journey-config.service';
import { ManagePayeesNavigationService } from '../../services/manage-payees-navigation.service';
import { NotificationService } from '@backbase/ui-ang/notification';

@Component({
  selector: 'bb-billpay-payee-details-view',
  templateUrl: './billpay-payee-summary-view.component.html',
})
export class PayeeSummaryViewComponent implements OnInit, OnDestroy {
  private readonly navigationExtras: NavigationExtras;
  private readonly selectedDeletePayeeSource = new BehaviorSubject<PayeeSummary | undefined>(undefined);
  private readonly destroy$ = new Subject<void>();
  payees$: Observable<PayeeSummary[]> = new Observable();
  isDeleteModalOpened = false;
  isNavigateModalOpened = false;
  private payeeSummaries!: PayeeSummary[];
  payeeSummary!: PayeeSummary;
  fullManualPayeeSummary?: Payee;
  payeeIndex = 0;
  @ViewChild('notificationMessage') notificationMessageTemplate: TemplateRef<any> | string = '';
  @ViewChild('notificationHeader') notificationHeaderTemplate: TemplateRef<any> | string = '';

  /** @internal */
  constructor(
    private readonly routerService: ManagePayeesNavigationService,
    private readonly route: ActivatedRoute,
    private readonly config: ManagePayeesJourneyConfigService,
    private readonly payeeSummaryService: BillpayPayeeSummaryWidgetService,
    private readonly notificationService: NotificationService,
    private readonly cdRef: ChangeDetectorRef,
  ) {
    this.navigationExtras = { relativeTo: this.route.parent };
  }

  gotoPayeeList() {
    this.routerService.navigateToPayeeList(this.navigationExtras);
  }
  gotoAddPayee() {
    this.routerService.navigateToSearch(this.navigationExtras);
  }
  gotoOneOffPayment(id: string) {
    this.routerService.navigateToOneOffPayment(id);
  }
  private gotoEditManual(id: string) {
    this.routerService.navigateToEditManualPayeeForm(id, this.navigationExtras);
  }
  private gotoEditElectronic(id: string) {
    this.routerService.navigateToEditElectronicPayeeForm(id, this.navigationExtras);
  }
  private gotoPaymentsList() {
    this.routerService.navigateToPendingPayments();
  }

  ngOnInit() {
    const id = this.route.snapshot.params['payeeID'] || undefined;
    this.payees$ = this.payeeSummaryService.getPayees().pipe(
      tap(data => this.setPayee(data, id)),
      tap(data => (this.payeeSummaries = data)),
    );
  }

  private setPayee(payeeSummaries: PayeeSummary[], id: string | undefined) {
    if (!payeeSummaries || payeeSummaries.length === 0) {
      return;
    } else if (id === undefined) {
      this.payeeSummary = payeeSummaries[0];
    } else {
      const payeeIndex = payeeSummaries.findIndex(payee => payee.id === id);
      if (payeeIndex > 0) {
        this.payeeIndex = payeeIndex;
        this.payeeSummary = payeeSummaries[payeeIndex];
      } else {
        this.payeeSummary = payeeSummaries[0];
      }
    }
    if (!this.payeeSummary.electronic) {
      this.payeeSummaryService
        .getFullSummary(this.payeeSummary.id)
        .pipe(takeUntil(this.destroy$))
        .subscribe(payee => {
          this.fullManualPayeeSummary = payee;
          this.cdRef.detectChanges();
        });
    }
  }

  handleEditPayee(): void {
    if (this.payeeSummary && this.payeeSummary.electronic) {
      this.gotoEditElectronic(this.payeeSummary.id);
    } else if (this.payeeSummary && !this.payeeSummary.electronic) {
      this.gotoEditManual(this.payeeSummary.id);
    }
  }

  onNavigateToPayments(resolve: boolean) {
    if (resolve) {
      this.gotoPaymentsList();
    }
    this.closeNavigateModal();
  }

  selectedDeletePayee(): Observable<PayeeSummary | undefined> {
    return this.selectedDeletePayeeSource.asObservable();
  }

  handleDeletePayee(): void {
    this.payeeSummary && !this.payeeSummary.nextPayment ? this.openDeleteModal() : this.openNavigateModal();
    this.selectedDeletePayeeSource.next(this.payeeSummary);
  }

  private openNavigateModal() {
    this.isNavigateModalOpened = true;
  }

  private closeNavigateModal() {
    this.isNavigateModalOpened = false;
  }

  private openDeleteModal() {
    this.isDeleteModalOpened = true;
  }

  private closeDeleteModal() {
    this.isDeleteModalOpened = false;
  }

  onDeletePayee(resolve: boolean) {
    this.closeDeleteModal();

    if (resolve && this.payeeSummary) {
      this.payeeSummaryService
        .deletePayee(this.payeeSummary.id, this.payeeSummary.electronic)
        .pipe(takeUntil(this.destroy$))
        .subscribe(() => {
          this.deletionNotify();
        });
      this.gotoPayeeList();
    }
  }

  private deletionNotify() {
    this.notificationService.showNotification({
      header: this.notificationHeaderTemplate,
      message: this.notificationMessageTemplate,
      modifier: 'success',
      ttl: this.dismissTimeoutValue,
    });
  }

  private get dismissTimeoutValue(): number {
    if (this.config.notificationDismissTime === undefined) {
      throw new Error('Dismiss timeout value not found');
    }
    return this.config.notificationDismissTime;
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private updatePayee() {
    if (this.payeeSummaries) {
      this.payeeSummary = this.payeeSummaries[this.payeeIndex];
      if (this.payeeSummary && !this.payeeSummary.electronic) {
        this.payeeSummaryService
          .getFullSummary(this.payeeSummary.id)
          .pipe(takeUntil(this.destroy$))
          .subscribe(payee => {
            this.fullManualPayeeSummary = payee;
            this.cdRef.detectChanges();
          });
      } else {
        this.fullManualPayeeSummary = undefined;
        this.cdRef.detectChanges();
      }
    }
  }

  nextPayee() {
    ++this.payeeIndex;
    this.updatePayee();
  }

  previousPayee() {
    --this.payeeIndex;
    this.updatePayee();
  }

  get currentLoadingState() {
    return this.payeeSummaryService.payeesSummaryLoadingState;
  }
}
