import { Injectable } from '@angular/core';
import {
  BillPayPayeesService,
  BillPaySearchService,
  BillPayPayeesPostRequestBody,
  BillPayPayeesPostResponseBody,
  PayeeByIdPutResponseBody,
  BillPayElectronicPayeesPostResponseBody,
  BillPayElectronicPayeesPostRequestBody,
  PayeeByIdPutRequestBody,
  PostElectronicRequestPayee,
  ElectronicPayeeByIdPutRequestBody,
  Payee,
  ElectronicPayeeByIdGetResponseBody,
  ElectronicPayeeByIdPutResponseBody,
  BillPaySearchGetResponseBody,
} from '@backbase/data-ang/billpay';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { ManualPayeeFormState, ElectronicPayeeForm } from '../model';

@Injectable()
export class BillpayManagePayeeDataService {
  /** @internal */
  constructor(
    private readonly dataService: BillPayPayeesService,
    private readonly searchService: BillPaySearchService,
  ) {}

  addManualPayee(payee: ManualPayeeFormState): Observable<BillPayPayeesPostResponseBody> {
    return this.dataService.postBillPayPayees({ billPayPayeesPostRequestBody: this.toPayeeRequest(payee) });
  }

  updateManualPayee(id: string, payee: ManualPayeeFormState): Observable<PayeeByIdPutResponseBody> {
    return this.dataService.putPayeeById({ payeeByIdPutRequestBody: this.toManualPutRequest(payee), id });
  }

  getManualPayee(id: string): Observable<Payee> {
    return this.dataService.getPayeeById({ id }).pipe(map(response => response.payee));
  }

  searchPayeesByName(name: string): Observable<BillPaySearchGetResponseBody> {
    return this.searchService.getBillPaySearch({ name });
  }

  addElectronicPayee(payee: PostElectronicRequestPayee): Observable<BillPayElectronicPayeesPostResponseBody> {
    return this.dataService.postBillPayElectronicPayees({
      billPayElectronicPayeesPostRequestBody: this.toElectronicPostRequest(payee),
    });
  }

  getElectronicPayee(id: string): Observable<ElectronicPayeeByIdGetResponseBody> {
    return this.dataService.getElectronicPayeeById({ id });
  }

  updateElectronicPayee(id: string, payee: ElectronicPayeeForm): Observable<ElectronicPayeeByIdPutResponseBody> {
    return this.dataService.putElectronicPayeeById({
      electronicPayeeByIdPutRequestBody: this.toElectronicPutRequest(payee),
      id,
    });
  }

  private toPayeeRequest(payee: ManualPayeeFormState): BillPayPayeesPostRequestBody {
    const {
      name,
      address: { address1, address2, city, state, postalCode },
      phoneNumber,
      accountNumber,
      nickName,
    } = payee;

    return {
      payee: {
        name,
        address: {
          address1,
          ...(address2 && { address2 }),
          city,
          state,
          postalCode,
        },
        ...(phoneNumber && { phoneNumber: phoneNumber.replace(/^\+1/, '').replace(/\D/g, '') }),
        ...(accountNumber && { accountNumber }),
        ...(nickName && { nickName }),
      },
    };
  }

  private toManualPutRequest(payee: ManualPayeeFormState): PayeeByIdPutRequestBody {
    return {
      payee: {
        ...this.toPayeeRequest(payee).payee,
        modifyPendingPayments: true,
      },
    };
  }

  private toElectronicPutRequest(payee: ElectronicPayeeForm): ElectronicPayeeByIdPutRequestBody {
    const { nickName, ...payeeWithoutNickname } = payee;

    return {
      payee: {
        ...payeeWithoutNickname,
        ...(nickName && { nickName }),
        modifyPendingPayments: true,
      },
    };
  }

  private toElectronicPostRequest(payee: PostElectronicRequestPayee): BillPayElectronicPayeesPostRequestBody {
    const { nickName, ...restProperties } = payee;

    return {
      payee: {
        ...(nickName && { nickName }),
        ...restProperties,
      },
    };
  }
}
