import { Component } from '@angular/core';
import { PayeeSearch } from '@backbase/data-ang/billpay';
import { BillpayUtilityService as Utility } from '@backbase/billpay-journeys-common';
import { ManagePayeesNavigationService } from '../../services/manage-payees-navigation.service';

@Component({
  templateUrl: './billpay-search-payee-view.component.html',
})
export class BillpaySearchPayeeViewComponent {
  private _searchAvailable = true;
  descTextId: string;

  /** @internal */
  constructor(private readonly router: ManagePayeesNavigationService) {
    this.descTextId = Utility.generateId();
  }

  createManualPayee() {
    this.router.navigateToManualPayeeForm();
  }

  onPayeeSelected(payee: PayeeSearch) {
    const { merchantID, zipRequired, name } = payee;
    this.router.navigateToElectronicPayeeForm(merchantID, { queryParams: { zipRequired, name } });
  }

  onSearchError() {
    this._searchAvailable = false;
  }

  onCanceled() {
    this.router.navigateToPayeeList();
  }

  get searchAvailable(): boolean {
    return this._searchAvailable;
  }
}
