import { ManagePayeesJourneyConfigService } from '../../services/manage-payees-journey-config.service';
import { Component, TemplateRef, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { ErrorItem, PostElectronicRequestPayee } from '@backbase/data-ang/billpay';
import {
  BillpayDiscardChangesModalService,
  BillpayNotificationRoutingService,
} from '@backbase/billpay-journeys-common';
import { BillpayManagePayeeStateService } from '../../services/billpay-manage-payee-state.service';
import { ManagePayeesNavigationService } from '../../services/manage-payees-navigation.service';
import { BillpayManagePayeeDataService } from '../../services/billpay-manage-payee-data.service';
import { ElectronicPayeeForm } from '../../model';
import { BaseElectronicPayeeReviewPageModelDirective } from '../../directives/base-electronic-payee-review-page.model';
import { NotificationService } from '@backbase/ui-ang/notification';
declare const $localize: any;

@Component({
  templateUrl: './billpay-create-electronic-payee-review-view.component.html',
  providers: [BillpayDiscardChangesModalService],
})
export class CreateElectronicPayeeReviewViewComponent extends BaseElectronicPayeeReviewPageModelDirective {
  @ViewChild('notificationHeader') notificationHeaderTemplate: TemplateRef<any> | string = '';
  @ViewChild('notificationMessage') notificationMessageTemplate: TemplateRef<any> | string = '';
  protected readonly payeeSubmit$ = new BehaviorSubject<PostElectronicRequestPayee | undefined>(undefined);
  private successfulPayeeId!: string;

  /** @internal */
  constructor(
    readonly config: ManagePayeesJourneyConfigService,
    readonly route: ActivatedRoute,
    stateService: BillpayManagePayeeStateService,
    discardModalService: BillpayDiscardChangesModalService,
    private readonly router: ManagePayeesNavigationService,
    private readonly dataService: BillpayManagePayeeDataService,
    private readonly notificationService: NotificationService,
    private readonly billpayNotificationRoutingService: BillpayNotificationRoutingService,
  ) {
    super(route, stateService, discardModalService, config);
  }

  submitPayee(id: string, payee: ElectronicPayeeForm) {
    this.payeeSubmit$.next({
      merchantID: id,
      ...payee,
    });
  }

  navigateBack(id: string) {
    this.router.navigateToElectronicPayeeForm(id, { queryParamsHandling: 'preserve' });
  }

  navigateToPayee() {
    this.billpayNotificationRoutingService.navigateToPayees();
  }

  navigateToNewPayment() {
    this.billpayNotificationRoutingService.navigateToNewPayment(this.successfulPayeeId);
  }

  protected onSubmitPayee(payee: PostElectronicRequestPayee) {
    this.dataService.addElectronicPayee(payee).subscribe(
      billPayPutPostResponse => {
        this.successfulPayeeId = billPayPutPostResponse.id;
        this.router.navigateToPayeeList();
        this.notificationService.showNotification({
          header: this.notificationHeaderTemplate,
          message: this.notificationMessageTemplate,
          modifier: 'success',
          ttl: this.dismissTimeout,
        });
      },
      ({ error }) => {
        const { header, message } = this.mapErrorNotificationContent(error?.errors);
        this.notificationService.showNotification({
          header,
          message,
          modifier: 'error',
          ttl: this.dismissTimeout,
        });
      },
    );
  }

  private mapErrorNotificationContent(errors: ErrorItem[] = []): { header: string; message: string } {
    if (errors.find(error => error.key === 'billpay.api.payeeAlreadyExists')) {
      return {
        header: $localize`:@@billpay.add-e-payee.alreadyExist-error-notification.header:Failed to add a new payee`,
        message: $localize`:@@billpay.add-e-payee.alreadyExist-error-notification.header:The payee could not be added because it already exists.`,
      };
    } else {
      return {
        header: $localize`:@@billpay.add-e-payee.default-error-notification.header:Something went wrong`,
        message: $localize`:@@billpay.add-e-payee.default-error-notification.header:We were unable to proceed. Please try again.`,
      };
    }
  }

  cancel() {
    this.router.navigateToPayeeList();
  }
}
