import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'bb-billpay-payveris-ebills-enrolment-error',
  templateUrl: './billpay-payveris-ebills-enrolment-error.component.html',
})
export class BillpayPayverisEbillsEnrolmentErrorComponent {
  /**
   * Selects the error element to display based on supported billpay api error codes.
   */
  @Input() errorState: string | undefined;
  @Output() returnToPayeeList = new EventEmitter();

  handleReturnToPayeeList() {
    this.returnToPayeeList.emit();
  }
}
