import { HostListener, OnDestroy, OnInit, Directive, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { filter, map, shareReplay, switchMap, take, takeUntil, tap } from 'rxjs/operators';

import {
  BillpayDiscardChangesModalService,
  BillpayDiscardChangesFormComponent,
} from '@backbase/billpay-journeys-common';
import { ElectronicPayeeForm } from './payee-form.model';
import { BillpayManagePayeeStateService } from '../services/billpay-manage-payee-state.service';

@Directive()
export abstract class BaseElectronicPayeeReviewPageModelDirective
  implements OnInit, OnDestroy, BillpayDiscardChangesFormComponent {
  readonly payee$: Observable<ElectronicPayeeForm | undefined>;
  readonly id$: Observable<string>;
  protected readonly destroy$ = new Subject<void>();
  @Input() dismissTimeoutValue: number | undefined;

  protected abstract readonly payeeSubmit$: BehaviorSubject<any>;
  abstract navigateBack(id: string): void;
  protected abstract onSubmitPayee(payee: any): void;

  /** @internal */
  protected constructor(
    protected readonly route: ActivatedRoute,
    protected readonly stateService: BillpayManagePayeeStateService,
    protected readonly discardModalService: BillpayDiscardChangesModalService,
  ) {
    this.payee$ = this.stateService.electronicPayeeFormState.pipe(take(1), shareReplay(1));

    this.id$ = this.route.paramMap.pipe(
      take(1),
      map(params => params.get('id') || ''),
    );
  }

  ngOnInit() {
    this.payee$
      .pipe(
        filter(payee => !payee),
        switchMap(() => this.id$),
      )
      .subscribe(id => {
        this.navigateBack(id);
      });

    this.payeeSubmit$.pipe(takeUntil(this.destroy$), filter(Boolean)).subscribe(payee => this.onSubmitPayee(payee));
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  @HostListener('window:beforeunload', ['$event'])
  onWindowUnload($event: BeforeUnloadEvent) {
    if (!this.isDiscardModalOpen) {
      $event.returnValue = true;
    }
  }

  get isDiscardModalOpen() {
    return this.discardModalService.isDiscardModalOpen;
  }

  protected get dismissTimeout(): number {
    if (this.dismissTimeoutValue === undefined) {
      throw new Error('Dismiss timeout value not found');
    }
    return this.dismissTimeoutValue;
  }

  openModal(): Observable<boolean> {
    return this.payeeSubmit$.pipe(
      switchMap(payee => (payee ? of(true) : this.discardModalService.openModal())),
      tap(isResolved => isResolved && this.stateService.resetElectronicPayee()),
    );
  }

  onModalResolve(resolvedWith: boolean) {
    this.discardModalService.onModalResolve(resolvedWith);
  }
}
