import { Injectable, Optional, Inject, InjectionToken } from '@angular/core';

export const ManagePayeesJourneyConfigurationToken = new InjectionToken<ManagePayeesJourneyConfiguration>(
  'ManagePayeesJourneyConfigurationToken injection token',
);

export interface ManagePayeesJourneyConfiguration {
  notificationDismissTime: number;
  multipleBillsMode: boolean;
}

const configDefaults: ManagePayeesJourneyConfiguration = {
  notificationDismissTime: 5000,
  multipleBillsMode: false,
};

@Injectable()
export class ManagePayeesJourneyConfigService {
  private _config: ManagePayeesJourneyConfiguration;

  constructor(@Optional() @Inject(ManagePayeesJourneyConfigurationToken) config: ManagePayeesJourneyConfiguration) {
    config = config || {};
    this._config = { ...configDefaults, ...config };
  }

  get defaults(): ManagePayeesJourneyConfiguration {
    return configDefaults;
  }

  get notificationDismissTime(): number {
    return this._config.notificationDismissTime;
  }
  get multipleBillsMode(): boolean {
    return this._config.multipleBillsMode;
  }
}
