import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError, debounceTime, distinctUntilChanged, filter, map, switchMap } from 'rxjs/operators';
import { BillpayManagePayeeDataService } from '../../../services/billpay-manage-payee-data.service';
import { PayeeSearch } from '@backbase/data-ang/billpay';

@Component({
  selector: 'bb-billpay-electronic-payee-search',
  templateUrl: './billpay-electronic-payee-search.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayElectronicPayeeSearchComponent {
  /**
   * Fires a payee's id when electronic payee is selected in the search box.
   */
  @Output() payeeSelected = new EventEmitter<PayeeSearch>();
  @Output() errorSearching = new EventEmitter();
  /**
   * Description text element id of the search input
   */
  @Input() descTextId?: string;
  readonly typeaheadOptions = {
    ngbTypeahead: this.search.bind(this),
    selectItem: this.onItemSelected.bind(this),
    resultFormatter: (payee: PayeeSearch) => payee.name,
    inputFormatter: (payee: PayeeSearch) => payee.name,
  };

  /** @internal */
  constructor(private readonly dataService: BillpayManagePayeeDataService) {}

  search(text$: Observable<string>): Observable<PayeeSearch[]> {
    const payees$ = (text: string) =>
      this.dataService.searchPayeesByName(text).pipe(
        map(({ payees }) => payees || []),
        catchError(() => {
          this.errorSearching.emit();
          return of([]);
        }),
      );

    return text$.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      filter(text => text.length >= 2),
      switchMap(payees$),
      catchError(() => of([])),
    );
  }

  onItemSelected(event: { item: PayeeSearch; preventDefault: () => void }) {
    this.payeeSelected.emit(event.item);
  }
}
