import { Injectable } from '@angular/core';
import {
  BillPayPayeesSummaryService,
  BillPayPayeesService,
  BillPayPayeesSummaryGetResponseBody,
  PayeeSummary,
  Payee,
} from '@backbase/data-ang/billpay';
import { catchError, map, tap } from 'rxjs/operators';
import { HttpResponse } from '@angular/common/http';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';

export const defaultDeleteParams = {
  cancelPendingPayments: false,
};

@Injectable()
export class BillpayPayeeSummaryWidgetService {
  private currentPayeeSummaryLoadingState = new BehaviorSubject<'loading' | 'error' | ''>('');

  /** @internal */
  constructor(
    private readonly payeesSummaryData: BillPayPayeesSummaryService,
    private readonly payeesData: BillPayPayeesService,
  ) {}

  getPayees(): Observable<PayeeSummary[]> {
    this.currentPayeeSummaryLoadingState.next('loading');
    return this.payeesSummaryData.getBillPayPayeesSummary().pipe(
      map(this.getPayeeSummaries),
      tap(data => sortPayees(data)),
      tap(() => this.currentPayeeSummaryLoadingState.next('')),
      catchError(() => {
        this.currentPayeeSummaryLoadingState.next('error');
        return EMPTY;
      }),
    );
  }

  deletePayee(id: string, electronic: boolean): Observable<HttpResponse<any>> {
    return electronic
      ? this.payeesData.deleteElectronicPayeeById({ ...defaultDeleteParams, id })
      : this.payeesData.deletePayeeById({ ...defaultDeleteParams, id });
  }

  getFullSummary(id: string): Observable<Payee> {
    return this.payeesData.getPayeeById({ id }).pipe(map(a => a.payee));
  }

  private getPayeeSummaries(response: BillPayPayeesSummaryGetResponseBody): Array<PayeeSummary> {
    // eslint-disable-next-line no-null/no-null
    if (response.payeeSummaries === null) {
      throw new Error('Response body not set');
    }
    return response.payeeSummaries ? response.payeeSummaries : [];
  }

  get payeesSummaryLoadingState() {
    return this.currentPayeeSummaryLoadingState;
  }
}

function getPayeeDisplayName(payee: PayeeSummary): string {
  return (payee.nickName || payee.name).toLowerCase();
}

function isEbillEnabled(payee: PayeeSummary): boolean {
  return payee.ebill ? payee.ebill.enabled : false;
}

function sortPayees(data: PayeeSummary[] | undefined) {
  if (data) {
    data.sort(function (payee1, payee2) {
      if (isEbillEnabled(payee1) && !isEbillEnabled(payee2)) return -1;
      if (!isEbillEnabled(payee1) && isEbillEnabled(payee2)) return 1;
      return getPayeeDisplayName(payee1) > getPayeeDisplayName(payee2) ? 1 : -1;
    });
  }
}
